import $ from 'jquery'
import './style.css'
// 引入模板
import sideNavHbs from './sideNav.hbs'

export default class leftPage {

  /**
   *  事件绑定
   */
  bindEvent() {
    $('.side-nav-btn').on('click', function(){
      let judge = $('.side-nav-btn').hasClass('active')
      if(judge) {
        $('.side-l-nav, .side-nav-btn').removeClass('active')
      } else {
        $('.side-l-nav, .side-nav-btn').addClass('active')
      }
    })
  }

  /**
   *  初始化
   */
  init(){
    const host = 'https://muzqi.github.io/Source/Release/jz-safetyTrend'
    $('.side-l-nav').html(sideNavHbs({
      data: [
        {
          name: '首页',
          href: `${host}/totalTrend.html?mock`,
          active: false
        }, {
          name: '终端安全监测',
          href: `${host}/terminalSecurity.html?mock`,
          active: false
        }, {
          name: '网络安全监测',
          href: `${host}/networkSecurity.html?mock`,
          active: false
        }, {
          name: '数据安全监测',
          href: `${host}/dataSecurity.html?mock`,
          active: false
        }, {
          name: '终端安全态势',
          href: `${host}/terminalTrend.html?mock`,
          active: true
        }
      ]
    }))
    this.bindEvent()
  }
}



